<template>
    <div>
       <el-button type="text" @click="outerVisible = true">选择区域</el-button>
        <el-dialog title="选择区域" :visible.sync="outerVisible" :modal-append-to-body="false">
            <el-input
                    placeholder="输入关键字进行过滤"
                    v-model="filterText">
            </el-input>
            <div  style="height: 400px;overflow-y: auto" >
                <el-tree
                        :filter-node-method="filterNode"
                        v-loading="!region.length"
                        :data="region"
                        show-checkbox
                        node-key="region_id"
                        :default-checked-keys="region_ids"
                        ref="tree"
                        @check="getCheckedKeys"
                        highlight-current
                        :props="defaultProps">
                </el-tree>
            </div>
            <div slot="footer" class="dialog-footer">
                <el-button size="mini" @click="outerVisible = false">取 消</el-button>
                <el-button size="mini" type="primary" @click="selReg">确 定</el-button>
            </div>
        </el-dialog>

    </div>
</template>

<script>
    import * as api from "@/api";
    export default {
        name: "RegionSelect",
        props:['regions','index'],
        data() {
            return {
                defaultProps: {
                    children: 'children',
                    label: 'label'
                },
                outerVisible:false,
                filterText: '',
                //整体列表 菊花
                loading: false,
                region:[],
                select_region:[],
                region_ids:[],
            }
        },
        created(){
            if(this.regions){
                this.region_ids = this.regions;
            }
            this.getRegion();
        },
        watch: {
            filterText(val) {
                this.$refs.tree.filter(val);
            }
        },
        methods: {
            selReg(){
                this.getCheckedKeys();
                this.$emit("selReg",{
                    index:this.index,
                    select_region:this.select_region,
                });
                this.outerVisible = false;
            },
            //获取全国城市
            getRegion(){
                api.post("/shop/admin.region/index", {
                    appToken: this.$store.getters.appToken,
                }).then(res => {
                    this.region = res.list;
                }).catch(() => {

                });
            },
            getCheckedKeys() {
                //所有选择的节点
                let checkeNodes = this.$refs.tree.getCheckedNodes();
                let halfCheckedKeys = this.$refs.tree.getHalfCheckedKeys();
                let region = [];
                checkeNodes.forEach(val => {
                    if (val.level == 1 || halfCheckedKeys.indexOf(val.parent_id) > -1) {
                        region.push({
                            label:val.label,
                            region_id:val.region_id,
                        } )
                    }
                });
                this.select_region = region;
            },

            filterNode(value, data) {
                if (!value) return true;
                return data.label.indexOf(value) !== -1;
            },
        }
    }
</script>

